import React, { useEffect, useState } from 'react';
import Header from './components/Header';
import MenuSuperior from './components/MenuSuperior';
import About  from './components/About';
import {Projects, ProjectFeatured, ProjectRight, ProjectLeft}  from './components/Projects';
import Fondo from './components/Fondo';

function App(){
    
  return (
    <>
      <MenuSuperior sections={[
          {id: 'about', title: 'About'},
          {id: 'projects', title: 'Projects'},
          {id: 'signup', title: 'Signup'}]}
          />
      <Fondo />

      <Header />
      
      <About />
      
      <Projects>
        <ProjectFeatured 
            description={"El mas importante"} 
            titulo={"Hola"} 
            imagePath={"assets/img/demo-image-02.jpg"} />
        <ProjectLeft
          description={"An example of where you can put an image of a project, or anything else, along with a description."} 
          titulo={"Misty"} 
          imagePath={"assets/img/demo-image-01.jpg"} />
        <ProjectFeatured 
          description={"El mas importante"} 
          titulo={"Hola"} 
          imagePath={"assets/img/demo-image-02.jpg"} />
        <ProjectRight
          description={"Another example of a project with its respective description. These sections work well responsively as well, try this theme on a small screen!"} 
          titulo={"Mountains"} 
          imagePath={"assets/img/demo-image-02.jpg"} />
      </Projects>
    </>
  );
}

export default App;
