import React, {ReactNode, useState} from 'react';
import { useEffect } from 'react';

interface ProjectData {
    titulo: string,
    description: string,
    imagePath: string
}

const ProjectFeatured = (props: ProjectData) => {
    const {titulo, description, imagePath} = props;

    return (
        <div className={"row gx-0 mb-4 mb-lg-5 align-items-center"}>
            <div className={"col-xl-8 col-lg-7"}><img className={"img-fluid mb-3 mb-lg-0"} src={imagePath} alt={"..."} /></div>
            <div className={"col-xl-4 col-lg-5"}>
                <div className={"featured-text text-center text-lg-left"}>
                    <h4>{titulo}</h4>
                    <p className={"text-black-50 mb-0"}>{description}</p>
                </div>
            </div>
        </div>
    );
};

const ProjectLeft = (props: ProjectData) => {
    const {titulo, description, imagePath} = props;

    return (
        <div className={"row gx-0 mb-5 mb-lg-0 justify-content-center"}>
            <div className={"col-lg-6"}><img className={"img-fluid"} src={imagePath} alt="..." /></div>
            <div className={"col-lg-6"}>
                <div className={"bg-black text-center h-100 project"}>
                    <div className={"d-flex h-100"}>
                        <div className={"project-text w-100 my-auto text-center text-lg-left"}>
                            <h4 className={"text-white"}>{titulo}</h4>
                            <p className={"mb-0 text-white-50"}>{description}</p>
                            <hr className={"d-none d-lg-block mb-0 ms-0"} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

const ProjectRight = (props: ProjectData) => {
    const {titulo, description, imagePath} = props;

    return (
        <div className={"row gx-0 justify-content-center"}>
        <div className={"col-lg-6"}><img className={"img-fluid"} src={imagePath} alt={"..."} /></div>
        <div className={"col-lg-6 order-lg-first"}>
            <div className={"bg-black text-center h-100 project"}>
                <div className={"d-flex h-100"}>
                    <div className={"project-text w-100 my-auto text-center text-lg-right"}>
                        <h4 className={"text-white"}>{titulo}</h4>
                        <p className={"mb-0 text-white-50"}>{description}</p>
                        <hr className={"d-none d-lg-block mb-0 me-0"} />
                    </div>
                </div>
            </div>
        </div>
    </div>
    );
};

const Projects: React.FC<{}> = ({children}) => {
    const [hijos, setHijos] = useState<ReactNode[]>(children as ReactNode[]);

    useEffect(() => {
        const intervalo = setInterval(() => {
            if(!hijos) return;
    
            setHijos(hijos.slice().sort(() => -0.5));
        }, 2000);

        return () => {
            clearInterval(intervalo); 
        }
      });

    

    return (<>
        <section className={"projects-section bg-light"} id="projects">
            <div className={"container px-4 px-lg-5"}>
                {hijos}
            </div>
        </section>    
    </>);
};

export {Projects, ProjectFeatured, ProjectLeft, ProjectRight};