import React, { useState } from 'react';
import PhotoModal from './PhotoModal';

function MenuSuperior(props: {sections: {id: string, title: string}[]}) {
    const {sections} = props;
    const [mostrarModal, setMostrarModal] = useState<Date>();

    const liSection = (item: {id: string, title: string}) => {
      const {id, title} = item;
  
      return (<li className="nav-item" key={id}><a className="nav-link" href={"#" + id}>{title}</a></li>);
    };

    return (
        <nav className={"navbar navbar-expand-lg navbar-light fixed-top"} id="mainNav">
            <div className={"container px-4 px-lg-5"}>
                <PhotoModal mostrar={mostrarModal} >
                    <img src={"/assets/img/logo.jpg"} style={{height: '30rem'}} alt={'Hernan Lavrencic'}
                    />
                </PhotoModal>
                <a className="navbar-brand" href={'#algo'} onClick={() => setMostrarModal(new Date())}>
                
                    <img src={"/assets/img/logo.jpg"} style={{
                        'borderTopLeftRadius': '50% 50%',
                        'borderTopRightRadius': '50% 50%',
                        'borderBottomRightRadius': '50% 50%',
                        'borderBottomLeftRadius': '50% 50%',
                        height: '50px', marginRight: '20px'}} alt={'Hernan Lavrencic'}
                    />
                    Hernan Lavrencic
                </a>
                <button className="navbar-toggler navbar-toggler-right" type="button" data-bs-toggle="collapse" data-bs-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                    Menu
                    <i className="fas fa-bars"></i>
                </button>
                <div className="collapse navbar-collapse" id="navbarResponsive">
                    <ul className="navbar-nav ms-auto">
                        {sections.map(liSection)}
                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default MenuSuperior;